.table-headers th {
  padding: 1rem;
  background-color: var(--peachy);
  border-radius: 2px;
}

td {
  padding: 1rem;
  border-radius: 2px;
}

tr:nth-child(odd) {
  background-color: rgb(253, 243, 241, 0.7);
}

.thumb-icon {
  transform: scale(1.4);
  cursor: pointer;
}

@media screen and (max-width: 500px) {
  .percentage {
    display: none;
  }
}
